<template>
	<div class="map-background">
		<div class="map-background__map" :style="{ height: h + 'px', margin: offset + 'px 0' }" >
			<div class="map-background__image" :style="style"></div>
			<div class="map-background__gradient"></div>
		</div>
	</div>
</template>

<script>



export default {

	props: {
		zoom: { type: Number, default: 1 },
		x: { type: Number, default: 0.5 },
		y: { type: Number, default: 0.5 },
		h: { type: Number, default: 500 },
		offset: { type: Number, default: 0 },
	},

	computed: {
		style(){
			const x = (this.x * 100) + '%';
			const y = (this.y * 100) + '%';
			return {
				backgroundSize: (this.zoom * 100) + '%',
				backgroundPositionX: x,
				backgroundPositionY: y,
			}
		}
	},

}
</script>

<style lang="scss" src="@/style/map-bg.scss"></style>